import React, { PureComponent, createRef } from "react";
import "./VerticalBarChart.scss";
import { Question, QuestionData } from "../../global-interfaces";
import { select, range, format, interpolateNumber, scaleLinear } from "d3";
import { max, ascending, descending } from "d3-array";
import Classnames from "classnames";
import { CHART_TYPES, CUST_PERSONS_ORDER, CUST_AGE_ORDER } from "../../constants";
import { checkIfMobile } from "../../utils";
const pct_format: any = format(".0%");
const { SUBSET } = CHART_TYPES

interface Props {
  chart: Question;
  color: string | null;
  inView: boolean;
  dots: boolean;
  verticalType: string;
}

class VerticalBarChart extends PureComponent<Props> {
  private chartRef: React.RefObject<HTMLDivElement>;
  constructor(props: Props) {
    super(props);
    this.chartRef = createRef();
  }
  componentDidUpdate(prevProps: Props) {
    const { data } = this.props.chart;
    select(this.chartRef.current)
      .selectAll(".label-data")
      .transition()
      .duration(500)
      .tween("html", function(_, j) {
        let old_value = prevProps.chart.data[j].value;
        let new_value = data[j].value;
        let that = select(this);
        let i = interpolateNumber(old_value, new_value);
        return function(t) {
          that.html(pct_format(i(t)));
        };
      });
  }

  render() {
    const { chart, color, inView, dots, verticalType } = this.props;
    const { data } = chart;

    const height =
      (this.chartRef.current && this.chartRef.current.offsetHeight) || 0;

    // single_top_label implies that this chart shares a subset (rather than sharing a measure)
    const single_top_label: boolean =
    verticalType === SUBSET && data[0].label_group === data[1].label_group;
    // single_top_label implies that this chart shares a subset (rather than sharing a measure)
    const single_lower_label: boolean =
    verticalType === SUBSET && data[0].label === data[1].label;

    const getAlignmentClass = (
      data: QuestionData[], 
      idx: number
    ) => {
      if (data.length == 2) {
        return `pos-${idx < data.length / 2
          ? "left" 
          : "right"}`
      } else return "pos-center"
    }

    const domainScale = (max: number) => scaleLinear()
      .domain([0, max])
      .range([0, 1])

    const getMax = (data: QuestionData[]) => max(data, d => d.value) || 0

    return (
      <div
        className={Classnames("VerticalBarChart", verticalType, color)}
        ref={this.chartRef}
      >
        {data
          .sort((a:any,b:any) => {
            if (a.id === "5_1_1_1_0") return ascending(CUST_PERSONS_ORDER.indexOf(a.label), CUST_PERSONS_ORDER.indexOf(b.label))
            if (a.id === "1_1_1_1_2") return ascending(CUST_AGE_ORDER.indexOf(a.label), CUST_AGE_ORDER.indexOf(b.label))
            else return ascending(a.value, b.value)
          })
          .map((d: any, i: any) => (
          <div
            key={i}
            className={Classnames(
              verticalType,
              "bar-group",
              getAlignmentClass(data, i)
            )}
          >
            {dots && <div className={Classnames("bar-dots", getAlignmentClass(data, i))}
            >
              {range(0, Math.floor(height / 15))
                .map((e: any, i: any) => (
                  <span key={"dot_" + i} className="dot" />
                ))
              }
            </div>}
            <div
              className={Classnames("bar-measure", verticalType, {
                growUp: inView,
              })}
              style={{
                /* dots imply a toggle, which would mean we need to scale the percent. Otherwise, take on custom domain scale. */
                height: dots 
                  ? pct_format(d.value)
                  : pct_format(domainScale(getMax(data))(d.value)),
              }}
            >
              <div
                className={Classnames("label-data", {
                  mid: dots && d.value < (checkIfMobile() ? 0.11 : 0.09),
                  small: dots && d.value < (checkIfMobile() ? 0.07 : 0.05),
                })}
              >
                {pct_format(d.value)}
              </div>
            </div>
            {single_lower_label && i === 0 ? null : (
              <div 
                className={Classnames("label-measure", {long: d.label.split(" ").reduce((t: boolean, v: string) => t || v.split('').length > 13, false)})}
                style={{
                  /* dots imply a toggle, which would mean we need to scale the label. Otherwise, take on custom domain scale. */
                  height: dots 
                    ? 'fit-content'
                    : pct_format(domainScale(getMax(data))(d.value)),
                }}
                ><span>{d.label}</span></div>
            )}
            {/* the "subset-label" appears at the top of the gas tank chart -- so it should only appear for subset charts, and if its a single top label, should only appear if i = 1. */}
            {verticalType !== SUBSET || (single_top_label && i === 0) ? null : (
              <div className="label-subset">{d.label_group}</div>
            )}
          </div>
        ))}
      </div>
    );
  }
}

export default VerticalBarChart;
