import React, { createRef, PureComponent } from "react";
import { createSelector } from "reselect";
import { group } from "d3-array";
import Classnames from "classnames";
import { returnFormatSrtringArray } from "../../utils"

import "./Card.scss";
import Toggle from "../Toggle";
import VerticalBarChart from "../VerticalBarChart";
import GroupedVerticalBar from "../GroupedVertical"
import HorizontalBarChart from "../HorizontalBarChart";
import PieChart from "../PieChart";
import WordCloud from "../WordCloud";

import { Question, section } from "../../global-interfaces";

import {
  checkIfMobile,
  generateCardID,
  getURLParams,
  updateURL,
} from "../../utils";
import {
  CHART_TYPES,
  SECTION,
  CATEGORY,
  CARD,
} from "../../constants";

const {
  SUBSET,
  VERT,
  HORIZ,
  PIE,
  WORD,
  GROUP_VERT,
  ANNOTATION
} = CHART_TYPES

/** INTERFACES */
interface Props {
  data: Question[];
  section: section;
  observer: IntersectionObserver;
  inView: boolean;
  isMobile: boolean;
}

interface State {
  /** tuple that holds the selected category for each position [0, 1, 2]*/
  selectedCategories: (string | null)[];
}

interface PositionGroup {
  id: string;
  position: 0 | 1 | 2;
  span: 1 | 2;
  data: Question;
  categories: (string | null)[];
}

interface ChartTypeProps {
  chartData: Question;
  color: string | null;
  title: boolean;
  toggle: boolean;
  inView: boolean;
}

interface ChartWrapperProps {
  positionGroup: PositionGroup;
  setCategory: Function;
  selectedCategory: string | null;
  color: string | null;
  inView: boolean;
  isMobile: boolean;
}

interface AnnotationProps {
  annotation: Array<TextObject>;
}

interface TextObject {
  label: string;
  label_group?: string;
  value?: number;
  column?: number;
}

class DetermineChartType extends PureComponent<ChartTypeProps> {
  render() {
    const { chartData, color, inView, toggle } = this.props;

    switch (chartData.chart_type) {
      case SUBSET:
        return (
          <VerticalBarChart chart={chartData} color={color} inView={inView} dots={false} verticalType={SUBSET}/>
        );

      case VERT:
        return (
          <VerticalBarChart chart={chartData} color={color} inView={inView} dots={toggle} verticalType={
            chartData.data.length > 2 
            ? "vertical-many" 
            : "vertical-two"}/>
        );

      case GROUP_VERT:
        return (
          <GroupedVerticalBar chart={chartData} color={color} inView={inView} />
        );

      case PIE:
        return (
          <PieChart
            chart={{
              ...chartData,
              data: chartData.data.sort((a: any, b: any) => b.value - a.value),
            }}
            color={color}
          />
        );

      case HORIZ:
        return (
          <HorizontalBarChart chart={chartData} color={color} inView={inView} />
        );

      case ANNOTATION:
        return <Annotation annotation={chartData.data} />;

      case WORD:
        return <WordCloud chart={chartData.data.map(d => d.label)} />;

      default:
        console.log("CHART ERROR:", chartData)
        return null
    }
  }
}

class ChartWrapper extends PureComponent<ChartWrapperProps> {
  render() {
    const {
      positionGroup,
      setCategory,
      selectedCategory,
      color,
      inView,
      isMobile,
    } = this.props;
    const { id, position, span, data, categories } = positionGroup;
    const title: boolean = !!data.graph_title;
    const toggle: boolean = !!selectedCategory;
    /* the data allows for legend dots in line with the text, and should be put in the spreadsheet with two curly braces such as {} this. */
    const titleSplitByLegend = data.graph_title.split("{}")

    return (
      <div
        className={Classnames(
          "chart-wrapper",
          id,
          `pos-${position}-${span}`,
          data.chart_type
        )}
      >
        {data.graph_title ? (
          <div
            className={Classnames("graph-title", {
              short: data.graph_title.length > (isMobile ? 90 : 200),
            })}
          >
            {titleSplitByLegend.map((d, i) => 
              <span 
                key={i}
                className={Classnames(i > 0 && "legend-dot")}
                >{d}</span>
            )}
          </div>
        ) : (
          <div />
        )}
        {selectedCategory ? (
          <Toggle
            selectedCategory={selectedCategory as string}
            categories={categories as [string, string]}
            setCategory={(cat: string) => setCategory(position, cat)}
          />
        ) : (
          <div />
        )}
        <DetermineChartType
          chartData={data}
          color={color}
          title={title}
          toggle={toggle}
          inView={inView}
        />
      </div>
    );
  }
}

class Annotation extends PureComponent<AnnotationProps> {
  render() {
    const { annotation } = this.props;
    return (
      <div
        className={Classnames("annotation", {
          "narrow-margin": annotation.length > 500,
        })}
      >
        <div className="content"
          style={{ 
            // if any of the following are a list, they should flex-start, otherwise, space-around.
            justifyContent: annotation.reduce((t,v) => t || v.label_group === "footnote", false) ? "space-between" : "flex-start" 
          }}>
          {annotation.map((d, i) => (
            <div key={i} className={d.label_group}>
              {returnFormatSrtringArray(d.label).map(([classKey, string], i) => <span key={i} className={classKey}>
                {string}
                </span>)}
            </div>
          ))}
        </div>
      </div>
    );
  }
}
/** SELECTORS */
const getData = (props: Props, _: State) => props.data;
const getSelectedCategories = (_: Props, state: State) =>
  state.selectedCategories;

const positionGroupSelector = createSelector(
  getData,
  //@ts-ignore
  data => Array.from(group(
      data, 
      (d: Question) => d.groupById), 
      ([groupById, data] : [string, Question[]]) => ({
        id: groupById,
        /* these assume all are the same, leverages the first */
        position: data[0].position,
        span: data[0].span,
        /* all questions for this section + card + position */
        data,
        /* toggle categories in the data, if any */
        categories: data.map((e: Question) => e.category),
      })
    )
);

const getFilteredPositionGroup = createSelector(
  positionGroupSelector,
  getSelectedCategories,
  (positionGroups, selectedCategories) =>
    positionGroups.map(d => ({
      position: d.position,
      span: d.span,
      id: d.id,
      data: d.data.find(
        e => e.category === selectedCategories[d.position]
      ) as Question,
      categories: d.categories,
    }))
);

/** COMPONENT */
class Card extends PureComponent<Props, State> {
  private cardRef: React.RefObject<HTMLDivElement>;
  constructor(props: Props) {
    super(props);
    const positionsGroup = positionGroupSelector(this.props, this.state);
    this.state = {
      selectedCategories: [null, null, null].map((d, i) => {
        const filteredPos = positionsGroup.find(p => p.position === i);
        return filteredPos ? filteredPos.categories[0] : null;
      }),
    };

    this.setCategory = this.setCategory.bind(this);
    this.cardRef = createRef();
  }

  componentDidMount() {
    const { data, section, observer } = this.props;
    const urlParams = getURLParams();
    // attach observer
    if (this.cardRef.current) observer.observe(this.cardRef.current);

    if (
      // 1. see if the urlParams refer to this card
      section.hash === urlParams[SECTION].hash &&
      data[0].card === urlParams[CARD] &&
      urlParams[CATEGORY]
    ) {
      // 2. if so check to see if those categories exist in the data (#failGracefully)
      const positionGroup = positionGroupSelector(this.props, this.state);
      const validCategories = (urlParams[CATEGORY] as []).reduce(
        (a, cat, i) => {
          const curPosition = positionGroup.find(pos => pos.position === i);
          // if that param doesn't exist in possible categories for that position, return false
          return curPosition ? curPosition.categories.includes(cat) && a : a;
        },
        true
      );

      // only setState if categories are valid (ie exist in this Card's data)
      if (validCategories)
        this.setState({
          selectedCategories: urlParams[CATEGORY] as string[],
        });
      else updateURL(section); // clean out URL
    }
  }

  setCategory(pos: 0 | 1 | 2, category: string | null) {
    this.setState(prevState => {
      const selectedCategories = [...prevState.selectedCategories];
      selectedCategories.splice(pos, 1, category);
      return { selectedCategories };
    });
  }

  render() {
    const { selectedCategories } = this.state;
    const { section, inView, isMobile } = this.props;

    const filteredData = getFilteredPositionGroup(this.props, this.state);
    const {
      card,
    } = filteredData[0].data;

    return (
      <div
        className={"Card"}
        id={generateCardID(section, card)}
        ref={this.cardRef}
      >
        {filteredData.map((d: PositionGroup) => (
          <ChartWrapper
            key={d.data.section + "-" + card + "-" + d.position}
            positionGroup={d}
            setCategory={this.setCategory}
            selectedCategory={selectedCategories[d.position]}
            color={section.color}
            inView={inView}
            isMobile={isMobile}
          />
        ))}
      </div>
    );
  }
}

export default Card;
