import React, { PureComponent, createRef, Fragment } from "react";
import "./HorizontalBarChart.scss";
import { Question } from "../../global-interfaces";
import { select, scaleLinear, range, format, interpolateNumber } from "d3";
import { group } from "d3-array";
import Classnames from "classnames";
import { checkIfMobile } from "../../utils";

const pct_format: any = format(".0%");

interface Props {
  chart: Question;
  color: string | null;
  inView: boolean;
}

interface State {
  width: number | null;
}

class HorizontalBarChart extends PureComponent<Props, State> {
  private chartRef: React.RefObject<HTMLDivElement>;
  constructor(props: Props) {
    super(props);
    this.state = {
      width: 0,
    };
    this.chartRef = createRef();
    this.onResize = this.onResize.bind(this);
  }

  componentDidMount() {
    window.addEventListener("resize", () => {
      this.onResize();
    });
    this.onResize();
  }

  componentWillUnmount() {
    //@ts-ignore
    window.removeEventListener("resize"); // prevent memory leaks
  }

  onResize() {
    const width =
      this.chartRef.current &&
      this.chartRef.current.getBoundingClientRect().width;
    this.setState({
      width,
    });
  }

  componentDidUpdate(prevProps: Props) {
    const { data } = this.props.chart;
    select(this.chartRef.current)
      .selectAll(".label-data")
      .transition()
      .duration(500)
      .tween("html", function(_, j) {
        let old_value = prevProps.chart.data[j].value;
        let new_value = data[j].value;
        let that = select(this);
        let i = interpolateNumber(old_value, new_value);
        return function(t) {
          that.html(pct_format(i(t)));
        };
      });
  }

  render() {
    const { chart, color, inView } = this.props;
    const { data, chart_type } = chart;
    const { width } = this.state;

    const nestedData = Array.from(group(data, d => d.label_group));
    const xScale = scaleLinear()
      .domain([1, 0])
      .range([width || 0, 0]);

    return (
      <div
        className={Classnames("HorizontalBarChart", color)}
        ref={this.chartRef}
      >
        {nestedData.map(([labelGroup, data]: any, i) => (
          <Fragment key={i}>
            {labelGroup !== "" 
              && <div className={"label-group-div"}>
                <hr />
                <span className={"label-group"}>{labelGroup}</span>
              </div>}
            {data.map((d: any, i: any) => {
              return (
                <div key={i} className={Classnames("bar-group")}>
                  <div
                    className={Classnames("bar-dots")}
                    style={{
                      width: width || 0,
                    }}
                  >
                    {range(0, Math.floor((width || 0) / 15)).map(
                      (e: any, i: any) => (
                        <span key={"dot_" + i} className="dot" />
                      )
                    )}
                  </div>
                  <div
                    className={Classnames("bar-measure", chart_type, {
                      growOut: inView,
                      "outside-bar": chart.span === 2 && !checkIfMobile() ? d.value < 0.07 : d.value < 0.12
                    })}
                    style={{
                      width: pct_format(d.value),
                    }}
                  >
                    <div className={"label-data"}
                      style={{ left: (d.value * (width || 0)) }}
                    >{pct_format(d.value)}</div>
                  </div>
                  <div
                    className={Classnames("label-inline")}
                    style={{
                      width: (width || 0) - (d.value * (width || 0)),
                      left: (d.value * (width || 0))
                    }}
                  >
                    {d.label}
                  </div>
                </div>
              );
            })}
          </Fragment>
        ))}
      </div>
    );
  }
}

export default HorizontalBarChart;
