import React, { PureComponent, createRef, Fragment } from "react";
import "./GroupedVerticalBar.scss";
import { Question, QuestionData } from "../../global-interfaces";
import { select, range, format, interpolateNumber, scaleLinear } from "d3";
import { mean, max, group } from "d3-array";
import Classnames from "classnames";
import { checkIfMobile } from "../../utils";
const pct_format: any = format(".0%");

interface Props {
  chart: Question;
  color: string | null;
  inView: boolean;
}

class GroupedVerticalBar extends PureComponent<Props> {
  private chartRef: React.RefObject<HTMLDivElement>;
  constructor(props: Props) {
    super(props);
    this.chartRef = createRef();
  }
  componentDidUpdate(prevProps: Props) {
    const { data } = this.props.chart;
    select(this.chartRef.current)
      .selectAll(".label-data")
      .transition()
      .duration(500)
      .tween("html", function(_, j) {
        let old_value = prevProps.chart.data[j].value;
        let new_value = data[j].value;
        let that = select(this);
        let i = interpolateNumber(old_value, new_value);
        return function(t) {
          that.html(pct_format(i(t)));
        };
      });
  }

  render() {
    const { chart, color, inView } = this.props;
    const { data } = chart;
    let vertical_type: string = "vertical-no-domain"

    const getAlignmentClass = (idx: number) => {
      return `pos-${idx === 0
        ? "left" : "right" }`
    }

    const groupedData = group(data, d => d.label_group)

    const domainScale = (max: number) => scaleLinear()
      .domain([0, max])
      .range([0, 1])

    const getMax = (data: QuestionData[]) => max(data, d => d.value) || 0

    return (
      <div
        className={Classnames("GroupedVerticalBar", vertical_type, color)}
        ref={this.chartRef}
      >
        {Array.from(groupedData.keys())
          .map((d: any, i: any) => (
          <div
            key={i}
            className={Classnames(
              vertical_type,
              "bar-group"
            )}
          >
            {
              //@ts-ignore
              groupedData.get(d).map((e, i) => 
                <div
                  key={i}
                  className={Classnames("bar-measure", 
                  getAlignmentClass(i), 
                  vertical_type, {
                    growUp: inView,
                  })}
                  style={{
                    height: pct_format(domainScale(getMax(data))(e.value))
                  }}
                >
                  <div
                    key={i}
                    className={Classnames("label-data", {
                      mid: vertical_type !== "vertical-no-domain" && e.value < (checkIfMobile() ? 0.11 : 0.09),
                      small: vertical_type !== "vertical-no-domain" && e.value < (checkIfMobile() ? 0.07 : 0.05),
                    })}
                  >
                    {pct_format(e.value)}
                  </div>
                </div>
                )
              }
              <div className="label-measure">{d}</div>
          </div>
        ))}
      </div>
    );
  }
}

export default GroupedVerticalBar;
